
.theme{width:100%;background:white
}
.theme .theme-banner{position:relative;width:100%;height:11.875rem;background:url("../../../static/images/live_front.jpg") no-repeat;background-position:center center;background-size:100% 100%
}
.theme .theme-list .theme-list-continer{margin:0 .8rem;overflow:hidden
}
.theme .theme-list .theme-list-continer p{font-size:0.9rem;border-bottom:1px solid #DDD;margin:0;padding:1rem 0
}
.theme .theme-list .theme-list-continer .theme-list-title{margin:.9rem 0
}
.theme .theme-list .theme-list-continer .theme-list-title span{display:inline-block
}
.theme .theme-list .theme-list-continer .theme-list-title span:nth-of-type(1){width:0.25rem;height:1.25rem;background:#1A73CD;line-height:1.875rem;vertical-align:middle
}
.theme .theme-list .theme-list-continer .theme-list-title span:nth-of-type(2){font-weight:700;margin-left:0.4rem
}
.theme .theme-list .theme-list-continer .theme-list-view .theme-list-view-item{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap !important;flex-wrap:wrap !important
}
.theme .theme-list .theme-list-continer .theme-list-view .theme-list-view-item a{-ms-flex:1;flex:1;display:block;height:10rem;text-decoration:none
}
.theme .theme-list .theme-list-continer .theme-list-view .theme-list-view-item a img{width:10.3rem
}
.theme .theme-list .theme-list-continer .theme-list-view .theme-list-view-item a p{display:block;color:#333;font-size:0.75rem;border:none
}
.theme .theme-list .theme-list-continer .theme-list-view .theme-list-view-item a:nth-child(even){text-align:right;margin-left:0.6rem
}
.theme .video-player{height:7rem !important
}
.theme .vjs-big-play-button{display:none !important
}
.theme .vjs-poster{height:7rem !important;background-size:cover !important
}
.theme .vjs-tech{height:7rem !important
}
.theme .vjs-text-track-display{height:7rem !important
}
.theme .vjs-error-display{height:7rem !important
}
.theme .video-js{height:7rem !important;background:url("../../../static/images/banner.jpg");background-repeat:no-repeat;background-position:cover;background-size:100% 100%
}



